export class ChecksModel {
	id: number;
	checkValue: string;
	checkCurrency: string;
	checkCurrencyId: number;
	checkNumber: string;
	dueDate: string;
	checkDate: string;
	checkStatus: string;
	checkStatusId: number;
	bankName: string;
	bankId: number;
	checkOwnerName: string;
	telephone: string;
	clientName: string;

	clear() {
		this.id = 0;
		this.checkValue = '';
		this.checkNumber = '';
		this.bankName = '';
		this.checkOwnerName = '';
		this.telephone = '';
		this.clientName = '';
		this.checkDate = new Date().toISOString().substr(0, 16);
		this.dueDate = new Date().toISOString().substr(0, 16);
	}
}
