// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of, Observable } from 'rxjs';
import { delay, map, startWith } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';
import { ChecksModel } from '../models/checks.model';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'gas-checks-edit-dialog',
	templateUrl: 'checks-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class ChecksEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	userChecks: ChecksModel;
	checkForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	myControl = new FormControl();
	filteredOptions: Observable<any[]>;
	selectedPumpId = '';
	isFinalChecks = false;
	// TODO: get using api
	banks = [];
	currencies = [];
	statuses = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<CheckssEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<ChecksEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.userChecks = this.data.check;
		this.statuses = this.data.statuses;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getBanks();
		this.getCurrencies();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let dueDate = this.userChecks.dueDate;
		if (this.userChecks.id > 0)
			dueDate = new Date(+new Date(this.userChecks.dueDate) - this.tzoffset).toISOString().substr(0, 16);

		let checkDate = this.userChecks.checkDate;
		if (this.userChecks.id > 0)
			checkDate = new Date(+new Date(this.userChecks.checkDate) - this.tzoffset).toISOString().substr(0, 16);

		this.checkForm = this.fb.group({
			id: [this.userChecks.id, Validators.required],
			checkValue: new FormControl(this.userChecks.checkValue, Validators.required),
			checkCurrency: new FormControl(this.userChecks.checkCurrencyId, Validators.required),
			checkNumber: new FormControl(this.userChecks.checkNumber, Validators.required),
			dueDate: [dueDate, Validators.required],
			checkDate: [checkDate, Validators.required],
			checkStatus: [this.userChecks.checkStatusId, this.userChecks.id > 0 ? Validators.required : ''],
			bankName: [this.userChecks.bankId, Validators.required],
			checkOwnerName: [this.userChecks.checkOwnerName, Validators.required],
			telephone: [this.userChecks.telephone, ''],
			clientName: [this.userChecks.clientName, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.userChecks.id > 0) {
			return `تعديل الحركة`;
		}

		return 'اضافة حركة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.checkForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getBanks() {
		this.http.get(
			globalVariables.baseUrl + 'api/Banks',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.banks = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});

			this.checkForm.controls["bankName"].setValue(this.userChecks.bankId);
		});
	}

	getCurrencies() {
		this.http.get(
			globalVariables.baseUrl + 'api/Currencies',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.currencies = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});

			this.checkForm.controls["checkCurrency"].setValue(this.userChecks.checkCurrencyId);
		});
	}

	/**
	 * Returns prepared customer
	 */
	prepareChecks(): ChecksModel {
		const controls = this.checkForm.controls;
		const _check = new ChecksModel();
		_check.id = this.userChecks.id;
		_check.checkValue = controls.checkValue.value;
		_check.checkCurrencyId = controls.checkCurrency.value;
		_check.checkNumber = controls.checkNumber.value;
		_check.checkDate = controls.checkDate.value;
		_check.dueDate = controls.dueDate.value;
		_check.checkStatusId = controls.checkStatus.value;
		_check.bankId = controls.bankName.value;
		_check.checkOwnerName = controls.checkOwnerName.value;
		_check.telephone = controls.telephone.value;
		_check.clientName = controls.clientName.value;

		return _check;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.checkForm.controls;

		/** check form */
		if (this.checkForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedChecks = this.prepareChecks();
		if (editedChecks.id > 0) {
			this.updateChecks(editedChecks);
		} else {
			this.createChecks(editedChecks);
		}
	}

	/**
	 * Update customer
	 *
	 * @param _check: ChecksModel
	 */
	updateChecks(_check: ChecksModel) {
		let checkDate = new Date(+new Date(_check.checkDate) - this.tzoffset).toISOString().slice(0, 16);
		let dueDate = new Date(+new Date(_check.dueDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/Checks',
			{
				..._check,
				checkValue: +_check.checkValue,
				checkDate: checkDate,
				dueDate: dueDate,
				checkNumber: +_check.checkNumber,
				bankId: _check.bankId,
				currencyId: _check.checkCurrencyId,
				checkStatusId: _check.checkStatusId
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _check, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل الحركة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create customer
	 *
	 * @param _check: ChecksModel
	 */
	createChecks(_check: ChecksModel) {
		let checkDate = new Date(+new Date(_check.checkDate) - this.tzoffset).toISOString().slice(0, 16);
		let dueDate = new Date(+new Date(_check.dueDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/Checks',
			{
				..._check,
				checkValue: +_check.checkValue,
				checkDate: checkDate,
				dueDate: dueDate,
				checkNumber: +_check.checkNumber,
				bankId: _check.bankId,
				currencyId: _check.checkCurrencyId
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _check, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة الحركة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
